import React, { Component } from 'react';
import './Skills.css';

class Skills extends Component {
    render() {
        return (
            <div className="Skills">

                <h3>Skills</h3>
                <h2>Work Skills and Methodologies</h2>
                <div className="row">
                    <Icons myIcon={iconsObj[0].icon} myTitle={iconsObj[0].name} />
                    <Icons myIcon={iconsObj[1].icon} myTitle={iconsObj[1].name} />
                    <Icons myIcon={iconsObj[2].icon} myTitle={iconsObj[2].name} />
                </div>
            </div>
        );
    }
}

const iconsObj = [
    {
        icon: <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M16.488 20l3.414 4h-2.627l-3.42-4h2.633zm-5.488 4h2v-4h-2v4zm-6.918 0h2.627l3.42-4h-2.633l-3.414 4zm4.918-18v7l7-3.398-7-3.602zm13-3v13h1v2h-22v-2h1v-13h-1v-3h22v3h-1zm-2 0h-16v13h16v-13z"/></svg>,
        name: 'Public Speaking'
    },
    {
        icon: <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M10.644 17.08c2.866-.662 4.539-1.241 3.246-3.682-3.932-7.427-1.042-11.398 3.111-11.398 4.235 0 7.054 4.124 3.11 11.398-1.332 2.455.437 3.034 3.242 3.682 2.483.574 2.647 1.787 2.647 3.889v1.031h-18c0-2.745-.22-4.258 2.644-4.92zm-12.644 4.92h7.809c-.035-8.177 3.436-5.313 3.436-11.127 0-2.511-1.639-3.873-3.748-3.873-3.115 0-5.282 2.979-2.333 8.549.969 1.83-1.031 2.265-3.181 2.761-1.862.43-1.983 1.34-1.983 2.917v.773z"/></svg>,
        name: 'Team Work',
    },
    {
        icon: <svg xmlns="http://www.w3.org/2000/svg"  viewBox="0 0 24 24"><path d="M1.979 8l-.979-6h6.972c1.265 1.583 1.327 2 3.306 2h11.722l-.96 4h-2.04l.469-2h-9.191c-2.326 0-3.166-.612-4.278-2h-3.694l.694 4h-2.021zm22.021 2l-2 12h-20l-2-12h6.278l2.308-3 3.9 3h1.97l-1.429-1.097 1.465-1.903 3.9 3h5.608zm-2.331 2h-19.338l1.385 8h16.568l1.385-8z"/></svg>,
        name: 'Organisation',
    },
]
class Icons extends Component {
    render() {
        return (
            <div>
                <span>

                    {this.props.myIcon}
                </span>
                <h4>{this.props.myTitle}</h4>
            </div>
        );
    }
}
export default Skills;