import React, { Component } from 'react';
import './getNavBar.css';

class NavBar extends Component {
    render() {
        const sections = ["Home", "About Me", "Projects", "Skills", "Contact"];
        const navLinks = sections.map(section => {
            return ( <li><a href={'#' + section}>{section}</a></li>);
        })
        return (
            <div className="wrapper">
                <nav className="navbar"> 
                    <h2 className="navheading">{this.props.portfolioHeading}</h2>
                    <ul >
                    <li className="First"><a href='#/home'>Home</a></li>
                    <li className="Second"><a href='#/about-me'>About Me</a></li>
                    <li className="Third"><a href='#/projects'>Projects</a></li>
                    <li className="Fourth"><a href='#/skills'>Skills</a></li>
                    <li className="Fifth"><a href='#/contact'>Contact</a></li>
                    </ul>
                </nav>
                </div>
        );
    }
}
export default NavBar;